export function throttle(func, wait) {
    let timeout;
    let isThrottled = false;
    return function () {
        if (isThrottled) {
            return;
        }
        const context = this;
        const args = arguments;
        func.apply(context, args);
        clearTimeout(timeout);
        timeout = setTimeout(() => (isThrottled = false), wait);
        isThrottled = true;
    };
}
