export class CanvasLib extends EventTarget {
    constructor(canvas) {
        super();
        this.canvas = canvas;
        this.plugins = new Map();
        const ctx = this.canvas.getContext("2d");
        if (ctx == null) {
            throw ('Could not get the context for the canvas');
        }
        this.context = ctx;
    }
    renderCycle() {
        this.paint();
        window.requestAnimationFrame(() => this.renderCycle());
    }
    use(PluginClass, settings) {
        const plugin = new PluginClass(this, settings);
        this.plugins.set(plugin.name, plugin);
    }
    getPlugin(PluginClass) {
        for (let plugin of this.plugins.values()) {
            if (plugin instanceof PluginClass) {
                return plugin;
            }
        }
        return undefined;
    }
    /**
     * Main paint function for the canvas
     *
     * @param shouldClear If true, clear the canvas before painting
     */
    paint(shouldClear = true) {
        this.prepareContext(shouldClear);
        const prop = { context: this.context };
        this.preRender(prop);
        this.render(prop);
        this.postRender(prop);
    }
    prepareContext(shouldClear = true) {
        this.context.resetTransform();
        if (shouldClear) {
            this.context.clearRect(0, 0, this.canvas.width, this.canvas.height);
        }
        this.context.translate(Math.floor(this.canvas.width / 2), Math.floor(this.canvas.height / 2));
    }
    /**
     * Just a utility function to make the user code less heavy
     */
    on(event, callback) {
        this.addEventListener(event, callback);
    }
    startRendering() {
        window.requestAnimationFrame(() => this.renderCycle());
    }
    preRender(eventProp) {
        const e = new CustomEvent('prerender', { detail: eventProp });
        this.dispatchEvent(e);
    }
    render(eventProp) {
        const e = new CustomEvent('render', { detail: eventProp });
        this.dispatchEvent(e);
    }
    postRender(eventProp) {
        const e = new CustomEvent('postrender', { detail: eventProp });
        this.dispatchEvent(e);
    }
}
